.flip-box {
  position: relative;
  width: 100%;
  max-width: 400px;
  display: inline-block;
  perspective: 600px;
  margin-bottom: 30px;
}

.flip-box:hover .flip_horizontal {
  transform: rotateY(180deg);
  z-index: 9;
}

.flip-box:hover .flip_vertical {
  transform: rotateX(180deg);
  z-index: 9;
}

.flip-box__strict {
  height: 176px;
}

@media screen and (max-width: 479px) {
  .flip-box__strict {
    height: 190px;
  }
}

.flip-box__strict-number {
  font-size: 13.375rem;
  line-height: 176px;
  margin-left: -10px;
}

.flip-box__strict-item {
  padding-top: 17px;
  padding-bottom: 10px;
  position: absolute;
  width: 100%;
  top: 0;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.94);
  overflow: hidden;
  transition: all 0.3s linear;
}

.flip-box__strict-title {
  position: absolute;
  background-color: #fff;
  width: 100%;
  top: 50%;
  transform: translateY(-50%);
  height: 44px;
  padding-top: 10px;
}

.flip-box__bg {
  background-position: center center;
  background-size: cover;
}

.flip-box__bg_overlay {
  position: relative;
}

.flip-box__bg_overlay:before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.6);
}

.flip-box__position {
  position: absolute;
  left: 0;
  width: 100%;
  z-index: 15;
}

.flip-box__position_35 {
  top: 35px;
}

@media screen and (max-width: 479px) {
  .flip-box__position_35 {
    top: 50%;
    transform: translateY(-50%);
  }
}

.flip-box__position_75 {
  top: 75px;
}

@media screen and (max-width: 479px) {
  .flip-box__position_75 {
    top: 50%;
    transform: translateY(-50%);
  }
}

.flip-box__gradient {
  height: 0;
  padding-bottom: 100%;
  min-height: 300px;
  box-shadow: 0 6px 30px rgba(0, 0, 0, 0.12);
}

.flip-box__gradient-pb100 {
  padding-bottom: 100%;
}

.flip-box__gradient-title {
  color: #fff;
  position: absolute;
  padding: 0 15px;
  width: 100%;
  top: 50%;
  left: 0;
  line-height: 32px;
  transform: translateY(-50%);
}

.flip-box__gradient-h4 {
  padding-left: 15px;
  padding-right: 15px;
  line-height: 32px;
}

.flip-box__multiply {
  min-height: 360px;
  box-shadow: 0 6px 30px rgba(0, 0, 0, 0.12);
  display: inline-block;
}

.flip-box__multiply-title {
  text-align: left;
  line-height: 74px;
  -webkit-text-fill-color: rgba(0, 0, 0, 0.3);
  color: rgba(0, 0, 0, 0.3);
  -webkit-background-clip: text;
  background-clip: text;
}

[dir="rtl"] .flip-box__multiply-title {
  text-align: right;
}

.flip-box__multiply-h4 {
  color: #fff;
}

.flip-box__multiply-decoration {
  background-position: center center;
  width: 56px;
  height: 56px;
  border-radius: 28px;
  color: #fff;
  position: relative;
  overflow: hidden;
}

.flip-box__multiply-decoration_square {
  border-radius: 0;
}

.flip-box__multiply-decoration:before {
  content: '';
  position: absolute;
  background-color: rgba(0, 0, 0, 0.3);
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
}

.flip-box__multiply-decoration i {
  position: absolute;
  top: calc(50%);
  left: calc(50% + 2px);
  transform: translate(-50%, -50%);
  z-index: 2;
}

.flip-box__multiply-btn {
  min-width: 180px;
  min-height: 48px;
}

.flip-box__multiply-btn.btn-inside-out > .before {
  padding-top: 12px;
}

.flip-box__multiply_dark .flip-box__multiply-title {
  -webkit-text-fill-color: rgba(0, 0, 0, 0.76);
}

.flip-box__multiply_dark .flip-box__multiply-decoration::before {
  background-color: rgba(0, 0, 0, 0.76);
}

.flip-box__multiply_dark .flip-box__bg_overlay:before {
  background-color: rgba(0, 0, 0, 0.76);
}

.flip-box__split {
  min-height: 300px;
  box-shadow: 0 6px 30px rgba(0, 0, 0, 0.12);
}

.brk-bordered-theme .flip-box__split {
