}

.flip-box__split-actions .added_to_cart {
  color: var(--brand-primary);
}

.flip-box__overlay-shortdesc {
  min-height: 330px;
  position: relative;
}

.flip-box__overlay-front, .flip-box__overlay-item {
  position: relative;
  z-index: 1;
}

.flip-box__overlay-item {
  padding-top: 55px;
}

.flip-box__overlay-item h3 {
  margin-top: 24px;
}

.flip-box__overlay-description {
  padding: 0 27px;
  color: #9f9f9f;
}

.flip-box__overlay-list {
  color: #272727;
  padding-left: 25px;
  padding-right: 10px;
  margin-top: 23px;
}

[dir="rtl"] .flip-box__overlay-list {
  padding-left: 10px;
  padding-right: 25px;
}

@media screen and (min-width: 992px) {
  .flip-box__overlay-list {
    padding-left: 65px;
  }
  [dir="rtl"] .flip-box__overlay-list {
    padding-right: 65px;
  }
}

.flip-box__overlay-list i {
  font-weight: 100;
  color: var(--brk-base-5);
  top: -4px;
  position: relative;
  padding-right: 20px;
  vertical-align: middle;
}

[dir="rtl"] .flip-box__overlay-list i {
  padding-right: 0;
  padding-left: 20px;
}

@media screen and (max-width: 479px) {
  .flip-box__overlay-list i {
    padding-right: 10px;
  }
}

.flip-box__overlay-btn {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, 50%);
  margin: 0;
}

.flip-box__overlay-title {
  color: var(--white);
}

.flip-box__overlay-excerpt {
