}

.flip-box__circle {
  height: 0;
  padding-bottom: 100%;
  position: relative;
}

.flip-box__circle-item {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 72.97297%;
  min-width: 240px;
  height: 72.97297%;
  min-height: 240px;
  perspective: 600px;
}

.flip-box__circle-front {
  padding-bottom: 100%;
  border-radius: 50%;
  background-image: linear-gradient(to top, rgba(var(--brand-primary-rgb), 0.94), rgba(var(--brk-base-2-rgb), 0.94));
  box-shadow: 0 6px 30px rgba(0, 0, 0, 0.12);
}

.flip-box__circle-back {
  background-color: #fff;
  border-radius: 50%;
}

.flip-box__circle-pretitle {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #fff;
  font-size: 8.5rem;
}

@media screen and (max-width: 479px) {
  .flip-box__circle-pretitle {
    font-size: 5.625rem;
  }
}

.flip-box__circle-content {
  position: absolute;
  top: calc(50% + 7px);
  left: 0;
  transform: translateY(-50%);
  padding: 0 15px;
  width: 100%;
}

.flip-box__circle-content h4 {
  padding: 0 5px;
}

.flip-box__circle-content h4 span {
  display: block;
  text-transform: none;
}

.flip-box__circle .divider-cross {
  margin: 16px auto 15px;
}

.flip-box__circle-excerpt {
  color: rbga(#2b2c2d, 0.5);
